.popup__container {
  display: block;
  width: 430px;
  background: #fff;
  border-radius: 10px;
  padding: 32px 36px 37px;
  position: relative;
  box-sizing: border-box;
}

@media (max-width: 765px) {
  .popup__container {
    padding: 25px 22px;
    width: calc(100% - 19px * 2);
  }
}
